export default {
	data() {
		return {
			notice:'',
			id:'',
			tznum:'',
			zynum:"",
		}
	},
	onShow() {
		let info = uni.getStorageSync('openId');
		if (!info || info == null || info == '') {
			uni.reLaunch({
				url: '../login/login'
			});
		}else{
			this.getinfo();
		}
	},
	
	methods: {
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/index/notice',
				data: this.getmd5({
					openId:uni.getStorageSync('openId'),
					
					// openId:'onR7C05SKrVWwNSZrlQgPbMEL4t4'
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						uni.hideLoading();
						var data = res.data.data;
						this.notice = data.informName;
						this.id = data.id;
						this.tznum = data.notiecNum;
						this.zynum = data.homeWorkNum;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
		godels(){
			uni.navigateTo({
				url:'../noticedel/noticedel?id='+this.id
			})
		},
		godel(url){
			if(url != ''){
				uni.navigateTo({
					url:'../'+url
				})
			}
		}
	}
}
